<template>
    <div @click="handleClick" class="ui-table__img">
        <div class="ui-image--static" v-show="src && !isComplete" :style="{width, height}"></div>
        <img v-show="false" :width="width" :height="height" :src="src" alt="" ref="myImg">
        <div v-if="isComplete" :style="{width, height, background: `url(${src}) no-repeat center/contain`}"></div>
        <div v-if="errorSrc" :style="{width, height, lineHeight: height}">
            <span class="icon-tupianshangchuan"></span>
        </div>
    </div>
</template>

<script>
    export default {
        name: "LoadingImg",
        props: {
            src: {
                type: String,
                default: ''
            },
            width: {
                type: String,
                default: '50px'
            },
            height: {
                type: String,
                default: '50px'
            }
        },
        data() {
            return {
                isComplete: false,
                errorSrc: ''
            }
        },
        methods: {
            handleClick(e) {
                if (!this.isComplete) return;
                this.$emit('click', e)
            }
        },
        mounted() {
            const vm = this;
            this.$nextTick(() => {
                if (this.src) {
                    this.$refs.myImg.addEventListener('load', function() {
                        vm.isComplete = true;
                    });

                    this.$refs.myImg.addEventListener('error', function() {
                        // vm.isComplete = true;
                        // vm.errorSrc = require('../assets/image/img_error.png');
                        this.errorSrc = '1';
                    })
                } else {
                    // this.isComplete = true;
                    // this.errorSrc = require('../assets/image/img_error.png');
                    this.errorSrc = '1';
                }
            })
        }
    }
</script>

<style scoped>
    .ui-table__img {
        /*background: #eeeeee;*/
        border: 1px solid #e6e6e6;
        display: inline-block;
        text-align: center;
    }

    .ui-table__img img {
        width: 100%;
        height: 100%;
    }

    .ui-image--static {
        background: url("../assets/image/img_loading.gif") no-repeat center/contain;
    }

    .icon-tupianshangchuan {
        font-size: 34px;
        vertical-align: middle;
        margin: 0;
    }
</style>


